// В этом файле должны быть стили для БЭМ-блока scroll-up, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.scroll-up
{
    display: none;

    width: 120px;
    height: 30px;

    transform: rotate(-90deg);
    position: fixed;
    left: 10px;
    bottom: 100px;
    z-index: 5;
}

.scroll-up__text
{
    font-size: 16px;
    font-variant: small-caps;

    display: block;

    margin-bottom: 5px;
}

.scroll-up__arrow
{
    position: relative;

    width: 100%;
    height: 1px;

    background-color: $black;
}

.scroll-up__arrow::before,
.scroll-up__arrow::after
{
    position: absolute;

    display: block;

    width: 6px;
    height: 1px;

    content: '';

    background-color: $black;
}

.scroll-up__arrow::before
{
    top: 2px;
    right: -2px;

    transform: rotate(-45deg);
}

.scroll-up__arrow::after
{
    top: -2px;
    right: -2px;

    transform: rotate(45deg);
}
@media screen and (max-width: 1023px) {
  .scroll-up {
    left: -30px;
  }
}
