// В этом файле должны быть стили для БЭМ-блока instagram-widget, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.instagram-widget {
    width: 100%;
}

.instagram-widget__head {
    flex-flow: row nowrap;
}

.head__arrow-text {
    text-transform: uppercase;
    padding: 0 70px;
}

.head__arrow-left,
.head__arrow-right {
    width: 100%;
    height: 18px;
    position: relative;
    border-top: 1px solid $black;
}

.head__arrow-left {
    border-right: 1px solid $black;
}

.head__arrow-right {
    border-left: 1px solid $black;
}

.head__arrow-left::before,
.head__arrow-left::after,
.head__arrow-right::before,
.head__arrow-right::after {
    content: "";
    display: block;
    width: 6px;
    height: 1px;
    position: absolute;
    background-color: $black;
    bottom: 2px;
}

.head__arrow-left::before {
    right: -6px;
    transform: rotate(-45deg);
}

.head__arrow-left::after {
    right: -1px;
    transform: rotate(45deg);
}

.head__arrow-right::before {
    left: -6px;
    transform: rotate(45deg);
}

.head__arrow-right::after {
    left: -1px;
    transform: rotate(-45deg);
}

.footer .owl-carousel {
    padding: 50px 30px;
    border-bottom: 1px solid $black;
}

.footer .owl-carousel .owl-item {
    padding-left: 0;
}

.footer .owl-carousel img {
    width: 100%;
    height: 100%;
    cursor: pointer;
}
.footer .owl-nav,
.footer .owl-dots {
    display: none;
}
