// В этом файле должны быть стили для БЭМ-блока header, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.header {
    border-bottom: 1px solid #8A8A8A;
    height: 80px;
    font-variant: small-caps;
}

.header__menu,
.header__search,
.header__socials {
    flex-basis: 33.33%;
}
.header__menu-holder {
    width: 100%;
    height: 100%;
}
.menu__list {
    align-items: center;
    justify-content: center;
    height: 100%;
}

.menu__list-item a,
.header__search label,
.socials__language {
    margin-left: $indent;
}
.menu__list-item a,
.socials__language {
    font-size: 110%;
}

.menu__list-item:first-child a {
    margin: 0;
}

.header__search {
    border-left: 1px solid #8A8A8A;
    border-right: 1px solid #8A8A8A;
    align-items: center;
}

.header__search label {
    display: block;
    width: 100%;
    height: 100%;
    text-transform: uppercase;
    margin: 0;
    padding: 0 20px;
}
.header__search input {
    border: none;
    outline: none;
    width: 100%;
    height: 100%;
    text-transform: uppercase;
}

.header__socials {
    justify-content: space-between;
    align-items: center;
}

.social__icon-link {
    margin-left: $text-indent;
}

.social__icon-link:first-child {
    margin: 0;
}

.main__logo {
    justify-content: center;
}
.hamburger, .close { display: none; }

input, textarea {
    @include input-placeholder {
        text-transform: uppercase;
    }
}

@media screen and (max-width: 1023px) {
  .menu__list-item a, .socials__language {
      margin-left: $text-indent;
  }
  .main__logo img {
      width: 100%;
  }
  .header__socials {
      padding-right: $text-indent;
  }
}
@media screen and (max-width: 767px) {
  .header {
      height: auto;
      min-height: 40px;
      z-index: 10;
  }
  .header__menu {
      flex-basis: 20%;
  }
  .header__search,
  .header__socials {
      flex-basis: 40%;
  }
  .hamburger {
      display: block;
      cursor: pointer;
      width: 100%;
      height: 100%;

    span {
      display: block;
      cursor: pointer;
      margin: 0.5*$text-indent $text-indent;
      position: relative;
      background: $black;
      width: 21px;
      height: 2px;
      position: absolute;
      top: 13px;

      &:before,
      &:after {
        content: '';
        display: block;
        height: 2px;
        background: $black;
        position: absolute;
        left: 0;
      }
      &:before {
        top: -6px;
        width: 28px;
      }
      &:after {
        bottom: -6px;
        width: 31px;
      }
    }
  }
  .close-menu {
    display: block;
    width: 60px;
    padding: 1.5*$text-indent 0;
    right: 17px;
    top: 8px;
    position: absolute;
    cursor: pointer;

    span {
      display: block;
      width: 60px;
      height: 2px;
      background: $black;
      transform: rotate(180deg);

      &:before,
      &:after {
        position: absolute;
        display: block;
        width: 6px;
        height: 2px;
        content: '';
        background-color: $black;
      }
      &:before {
        top: 2px;
        right: 0;
        transform: rotate(-45deg);
      }
      &:after {
        top: -2px;
        right: 0;
        transform: rotate(45deg);
      }
    }
  }
  .header__menu-holder {
    width: 96%;
    height: auto;
    background: $white;
    top: 0;
    margin: 0;
    padding: 4*$text-indent 2*$text-indent $text-indent;
    overflow: hidden;
    position: absolute;
    display: none;
    -moz-box-shadow: 0 5px 5px rgba(0,0,0,.3);
    -webkit-box-shadow: 0 5px 5px rgba(0,0,0,.3);
    box-shadow: 0 5px 5px rgba(0,0,0,.3);
  }
  .menu__list-item {
    width: 100%;
    padding: $text-indent 0;

    a {
      margin: 0;
      font-size: $font-size--h3;
    }
  }
}
@media screen and (max-width: 480px) {
  .header__socials {
    padding-right: $text-indent;
  }
}
