// В этом файле должны быть стили для БЭМ-блока content, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.content {
    width: calc(100% - 225px);
    padding-right: 85px;
}

.content__text {
    margin-top: $indent;
}

.content h1 {
    font-size: $font-size--h1;
    text-transform: uppercase;
    font-weight: 800;
    margin: 10px 0;
}

.content p {
    font-size: $font-size + 4;
    padding: 10px 0;
    text-align: justify;
}

.content__img img {
    display: block;
    width: 630px;
    height: 805px;
}

.categories__block {
    margin-top: 75px;
}

.categories__block:first-child {
    margin: 0;
}

.categories__block-img img {
    width: 100%;
    display: block;
}

.categories__block-title {
    align-items: center;
}

.categories__block-link {
    border: 1px solid $black;
    padding: 0 37px;
    line-height: 35px;
    margin-left: $indent;
}

.categories__block h1 {
    font-weight: 400;
}

.categories__block-description {
    margin-top: $indent - 10px;
}

.categories__block-description p {
    font-size: $font-size + 2;
}

.content__pagiantion {
    margin-top: $indent;
    justify-content: center;
}

.pagination__prev,
.pagination__next {
    align-items: center;
}

.pagination__next {
    margin-left: $indent;
}

.pagination__prev .pagination__arrow {
    margin-right: $text-indent;
}

.pagination__next .pagination__arrow {
    margin-left: $text-indent;
}

.pagination__arrow {
    width: 65px;
    height: 1px;
    background-color: $black;
    position: relative;
}

.pagination__arrow::before,
.pagination__arrow::after {
    content: "";
    display: block;
    width: 6px;
    height: 1px;
    position: absolute;
    background-color: #000;
}

.pagination__prev .pagination__arrow::before {
    content: "";
    top: -2px;
    left: 0;
    transform: rotate(-45deg);
}

.pagination__prev .pagination__arrow::after {
    content: "";
    top: 2px;
    left: 0;
    transform: rotate(45deg);
}

.pagination__next .pagination__arrow::before {
    content: "";
    top: -2px;
    right: 0;
    transform: rotate(45deg);
}

.pagination__next .pagination__arrow::after {
    content: "";
    top: 2px;
    right: 0;
    transform: rotate(-45deg);
}

.content__galery a {
    float: left;
    height: auto;
    margin-bottom: 10px;
}

.content__galery a img {
    width: 100%;
    height: 100%;
}

.content__galery > a:nth-child(1) {
    width: 65%;
    padding-right: 10px;
}

.content__galery > a:nth-child(2) {
    width: 35%
}

.content__galery > a:nth-child(3) {
    width: 100%;
}

.content__galery > a:nth-child(4) {
    width: 40%;
    padding-right: 10px;
}

.content__galery > a:nth-child(6) {
    width: 100%;
}

.content__galery--indent {
    width: 60%;
    float: left;
}

.remodal-wrap {
    width: 100%;
    height: 100%;
}

.remodal__img {
    max-width: 400px;
    max-height: 400px;
}

.remodal__img img {
    width: 100%;
    height: 100%;
}

.remodal__content {
    max-width: 400px;
    margin-left: $indent;
    text-align: left;
}

.remodal h1 {
    font-size: $font-size--h1;
    margin: $text-indent 0;
}

.remodal p {
    font-size: $font-size;
    line-height: $line-height;
}

.remodal p,
.remodal span {
    padding: $text-indent 0;
}

.remodal__link {
    line-height: 35px;
    align-items: center;
    border: 1px solid $black;
    width: 120px;
    justify-content: center;
}

.remodal__link p,
.remodal__link span {
    padding: 0 !important;
}

.remodal__link-arrow {
    display: block;
    width: 50px;
    height: 1px;
    background-color: $black;
    margin-left: $text-indent;
    position: relative;
}

.remodal__link-arrow::before,
.remodal__link-arrow::after {
    content: "";
    position: absolute;
    display: block;
    width: 6px;
    height: 1px;
    background-color: $black;
    right: 0;
}

.remodal__link-arrow::before {
    content: "";
    transform: rotate(45deg);
    top: -2px;
}

.remodal__link-arrow::after {
    content: "";
    transform: rotate(-45deg);
    top: 2px;
}
@media screen and (max-width: 1023px) {
  .content {
    width: 100%;
    padding: 0 $indent;
  }
  .content__img img {
    width: 100%;
    height: auto;
  }
  .content__galery {
    margin-bottom: $indent;
  }
}
@media screen and (max-width: 767px) {
  .content {
    padding: 0;
  }
  .content__text,
  .categories__block-title,
  .categories__block-description {
    padding: 0 2*$text-indent;
  }
  .categories__block-title > div {
    width: 100%;
  }
  .categories__block h1 {
    width: 100%;
  }
  .categories__block-link {
    margin: 2*$text-indent 0 0;
  }
}
