// В этом файле должны быть стили для БЭМ-блока aside, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.aside
{
    width: 225px;
    height: 100%;
}

.aside__form
{
    height: 110px;
    align-items: center;
    border-top: 1px solid $black;
    border-bottom: 1px solid $black;
}

.aside__form input {
    height: 35px;
    width: 140px;
    margin-right: $text-indent;
    border: 1px solid $black;
    padding-left: $text-indent;
}

.aside__form-button {
    width: 75px;
    height: 35px;
    color: #fff;
    cursor: pointer;
}

.aside__form {
    @include input-placeholder {
        color: $black;
    }
}

.aside__preview {
    padding-top: $indent;
}

.aside__preview a {
    display: block;
    margin-bottom: $indent;
}

@media screen and (max-width: 1023px) {
  .aside {
    width: 100%;
    height: auto;
    padding: 0 2*$text-indent;
    display: flex;
    flex-flow: row wrap;
  }
  .aside__form {
    width: 100%;
    order: 2;
    border: none;
    justify-content: center;
  }
  .aside__preview {
    width: 100%;
    order: 1;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;

    a {
      width: 30%;
      max-height: 204px;
      overflow: hidden;

      img {
        height: 100%;
        width: 100%;
        object-fit: cover;
        object-position: center;
      }
    }
  }
}
@media screen and (max-width: 767px) {
  .aside__preview {
    display: none;
  }
}
