// В этом файле должны быть стили для БЭМ-блока slider, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.slider {
    justify-content: center;
    padding: 0 $text-indent;
}

.owl-prev,
.owl-next {
    background: url("../img/strelka.svg") no-repeat center center;
    width: 145px;
    height: 55px;
    background-size: contain;
    position: absolute;
    opacity: 0.3;
    transition: all 0.35s linear;
}

.owl-prev:hover,
.owl-next:hover {
    opacity: 1;
    width: 150px;
    height: 60px;
}

.owl-prev {
    top: 50%;
    left: -8%;
    transform: translateY(-50%) rotate(180deg);
}

.owl-next {
    top: 50%;
    right: -8%;
    transform: translateY(-50%);
}

.slider__description {
    margin-top: $indent;
}

@media screen and (max-width: 1024px) {
  .owl-prev, .owl-next {
    width: 106px;
    height: 42px;

    &:hover {
      width: 111px;
      height: 47px;
    }
  }
  .owl-prev {
    left: -10px;
  }
  .owl-next {
    right: -10px;
  }
}
@media screen and (max-width: 767px) {
  .slider {
    display: none;
  }
}
@media screen and (max-width: 480px) {
  .owl-prev, .owl-next {
    width: 42px;
    height: 18px;
    opacity: 1;

    &:hover {
      width: 44px;
      height: 20px;
    }
  }
}
