*,
*:before,
*:after
{
    box-sizing: border-box;
}

body
{
    font-family: 'NewGoth Normal', sans-serif;
    font-size: $font-size;

    width: 100%;
    height: 100%;
    min-height: 100vh;

    color: $text-color;
    background: $body-bg;
}

a
{
    text-decoration: none;

    color: $black;
}

a:hover
{
    color: inherit;
}

select
{
    line-height: 1em;

    cursor: pointer;
    text-indent: .01px;
    text-overflow: '';

    appearance: none !important;
}

select::-ms-expand
{
    display: none;
}

button
{
    border: none;
    outline: none;
    background-color: $button-color;
}

.main__content
{
    margin-top: 50px;
    width: 100%;
}

.visually-hidden
{
    position: absolute;

    visibility: hidden;

    width: 0;
    height: 0;

    opacity: 0;
}

main
{
    margin-bottom: 40px;
    padding: 0 10px;
    width: 100%;
}

.hidden
{
    display: none;
}

.flex
{
    display: flex;

    flex-flow: row wrap;
}

.container-inner
{
    display: flex;

    justify-content: center;
}

.container
{
    display: flex;

    width: 100%;
    max-width: 960px;

    flex-flow: row wrap;
}

.container--flex
{
    display: flex;

    flex-flow: row nowrap;
}

.main.container-inner
{
    flex-direction: column;

    align-items: center;
}

.index-page-container
{
    flex-flow: row wrap;
}

.page {
    display: flex;
    justify-content: center;
    flex-flow: column wrap;
    width: 100%;
    align-items: center;
}

@media screen and (max-width: 1023px) {
  .main {
    padding: 0;
    margin-bottom: 20px;
  }
}
@media screen and (max-width: 767px) {
  .page {
    padding: 0;
  }
  .main {
    padding: 0 $text-indent;
  }
  .main__content {
    margin-top: 20px;
  }
}
@media (max-width: 480px)
{
    .container--flex
    {
        flex-flow: column nowrap;
    }
}
