// В этом файле должны быть стили для БЭМ-блока catalog, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.catalog
{
    width: calc(100% - 225px);
}

.catalog__item
{
    width: 100%;
    margin-bottom: $indent;
}

.catalog__item-header,
.catalog__item-header-img,
.catalog__item-header-img img
{
    width: 100%;
    height: 100%;
}

.catalog__item-header
{
    max-height: 550px;
}

.catalog__item-header-img
{
    max-width: 480px;
}

.catalog__item-header-img,
.catalog__item-header-img img
{
    display: block;
}

.catalog__item-header-detail
{
    width: 100%;
    max-width: 160px;
    margin-left: $indent;
    padding: $text-indent;

    text-align: center;

    border: 1px solid $black;

    align-items: flex-end;
    justify-content: center;
}

.catalog__item-header-detail-container
{
    width: 100%;
}

.detail__block
{
    width: 100%;
    height: 60px;
    margin-top: 15px;

    border: 1px solid $black;

    align-items: center;
    justify-content: center;
}

.detail__block-arrow
{
    position: relative;

    width: 80%;
    height: 1px;

    background-color: $black;
}

.detail__block-arrow::before,
.detail__block-arrow::after
{
    position: absolute;

    display: block;

    width: 6px;
    height: 1px;

    content: '';

    background-color: $black;
}

.detail__block-arrow::before
{
    top: 2px;
    right: -2px;

    transform: rotate(-45deg);
}

.detail__block-arrow::after
{
    top: -2px;
    right: -2px;

    transform: rotate(45deg);
}

.catalog__item-text
{
    width: 100%;
    max-width: 480px;
    margin-top: $text-indent;
}

.catalog__item-text-date
{
    margin-top: $indent;

    text-align: center;
}
.post-nav {
  max-width: 480px;
}
.post-nav-prev,
.post-nav-next
{
    width: 150px;
    height: 60px;
    margin: 0 $text-indent $text-indent;

    text-transform: uppercase;

    border: 1px solid $black;

    align-items: center;
    justify-content: center;
}

@media screen and (max-width: 1023px) {
  .catalog {
    width: 100%;
    padding: 0 20px;
    position: relative;
  }
  .catalog__item {
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
  }
  .catalog__item-header {
    margin: 0 0 $indent;
    justify-content: center;
  }
}
@media screen and (max-width: 767px) {
  .catalog {
    padding: 0;
  }
  .catalog__item-header-detail {
    display: none;
  }
  .catalog__item-text {
    padding: 0 2*$text-indent;
  }
  .post-nav-prev,
  .post-nav-next {
    width: 40%;
  }
}
